.wrapper {
  background: #7E8CA3;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.calculator {
  font-family: 'Assistant', sans-serif;
  font-weight: 200;
  display: flex;
  flex-direction: column;
  max-width: 220px;
  margin: auto; }
  .calculator .display {
    flex: 1 1 100%;
    height: 80px;
    background: #113B51;
    color: #e2e2e2;
    display: flex;
    flex-direction: column;
    text-align: right;
    padding-right: 15px; }
    .calculator .display .helper-display {
      flex: 1 1;
      padding-top: 10px;
      font-size: 1.1em; }
    .calculator .display .main-display {
      flex: 2 1;
      padding-top: 8px;
      font-size: 2em;
      font-weight: 300; }
  .calculator .buttons {
    display: flex;
    flex-wrap: wrap; }
    .calculator .buttons button {
      font-family: 'Assistant', sans-serif;
      font-weight: 300;
      flex: 1 1 25%;
      border: none;
      height: 45px;
      background: #477187;
      color: #FAE25D;
      transition: 0.5s ease-out; }
      .calculator .buttons button:hover {
        cursor: pointer;
        background: #AA7639; }
      .calculator .buttons button.num {
        color: #e2e2e2; }
      .calculator .buttons button.double {
        flex: 1 1 50%; }
